<template lang="pug">
.curso-main-container.pb-3
  BannerInterno
  .container.tarjeta.tarjeta--blanca.p-4.p-md-5

    .titulo-principal.color-acento-contenido
      .titulo-principal__numero
        span.text-white 1
      h1 Requerimientos

    .row.mb-4
      .col-lg-6
        .cajon.color-primario.p-4.mb-4.mb-lg-0(style='background-color: #ECF7FF')
          p.mb-0 Los requerimientos son las peticiones o solicitudes que se consideran necesarias o importantes para satisfacer una o varias necesidades, ante un problema o mejora de un proceso.    
        p.m-2 Es decir, son las condiciones o capacidades que deben tener los sistemas, productos o servicios para lograr satisfacer un contrato, estndar o algunas otras especificaciones o documentos que se hayan establecido formalmente.<br><br>Los requerimientos no indican el diseo que debe tener el producto, indican las funciones y contenidos que se espera que tenga y la manera cmo los usuarios interactan con l; los requerimientos varan con el tiempo, puesto que, con la puesta en marcha del producto, se podrn generar nuevos requerimientos (diferentes o complementarios a los iniciales).
      .col-lg-6
        .imagen        
          img(src='@/assets/curso/imgtem01.jpg', style="width: 400px") 
    
    .row.align-items-center.mb-3
      .col-lg-5
        .imagen        
          img.m-2(src='@/assets/curso/imgtem02.jpg', style="width: 400px") 
      .col-lg-7
        .cajon.color-acento-botones.p-4.mb-4.mb-lg-0(style='background-color: #FEF4D6')
          p.mb-0 El propsito principal de los requerimientos es asegurar la satisfaccin de las expectativas de los clientes y sus interesados (<i> stakeholders </i>), internos y externos, relacionando de manera eficiente, los vnculos que esperan los clientes y usuarios, y lo que el grupo del proyecto tiende a desarrollar.
        p.m-2 Ahora bien, los requerimientos son adaptados a todo tipo de proyectos, as, se trata de garantizar el ptimo funcionamiento de los procesos, sirviendo de referencia para el aseguramiento y control de los cambios que se pueden presentar en el proyecto (trazabilidad).
      
    .row.justify-content-start.align-items-center
      .col-lg-3.cajontitle2.mb-4(data-aos="zoom-in")      
        h3.text.mb-0 Control de cambios
    
    .row.mb-4
      .col-lg-7
        p.align-center(data-aos="zoom-in-right") Durante la ejecucin del proyecto se encuentran muchas situaciones, en las que se identifican nuevas necesidades, las cuales ni siquiera se haban pensado, pero se hace necesario anexarlas a la aplicacin.<br><br>Estos cambios imprevistos, hacen que se replanteen nuevos requerimientos e incluso requerimientos ya documentados.<br><br>Se debe definir desde un principio la forma en que se gestionarn dichos cambios, debido a que pueden generar retrocesos en el desarrollo y en la planeacin.<br><br>Se deben incluir clusulas contractuales claras para que las partes a intervenir lo tengan en cuenta, y no se tenga que retroceder en lo que se ha desarrollado.
      .col-lg-3
        img.imgpos3.m-4(src='@/assets/curso/imgtem03.jpg')(data-aos="zoom-in-left")    
    
    .row.mb-4
      h3(data-aos="zoom-out-right") Tcnicas en el levantamiento de requerimientos
    
    .row.mb-5
      p.col12(data-aos="zoom-out-right") La etapa fundamental para la creacin de un sistema, dentro de un proyecto informtico, es la identificacin y documentacin de los requerimientos del sistema al iniciar el proyecto, puesto que en muchas situaciones se plantean ideas y se ayudan a prevenir ciertos errores que se puedan presentar, evitando as el fracaso del mismo.<br><br>El levantamiento de requerimientos hace referencia a la identificacin y documentacin del sistema, partiendo de usuarios, clientes o interesados; tambin se le llama recopilacin de requerimientos.<br><br>A continuacin, se muestran algunas tcnicas para obtener requerimientos en los proyectos de <i>software</i>:
    
    .row.mb-5
      .col-lg-7
        .row.mb-5(data-aos="fade-up")
          .col-md-12.mb-4.mb-md-0
            LineaTiempoD.color-acento-contenido.text-bold
              p.text-small(numero="1" titulo="Anlisis de documentacin").m-3
                ul.lista-ul(style="list-style-type: square").lista-ul.lista-ul--color
                  li 
                    i.fas.fa-angle-right 
                    p Es donde se obtiene la informacin sobre los requerimientos funcionales y requerimientos no funcionales partiendo de documentos ya elaborados.
                  li 
                    i.fas.fa-angle-right 
                    p Se utiliza cuando los expertos en el desarrollo no estn disponibles para ser interrogados o ya no forman parte del proceso o la empresa.
                  li 
                    i.fas.fa-angle-right 
                    p Evitar riesgos al no tener documentacin. En el momento de necesitarla, se tendr que hacer en el momento menos oportuno y sin recordar nada, perdiendo informacin valiosa y tiempo, buscando las soluciones.
              
              p.text-small(numero="2" titulo="Observacin").m-3
                ul.lista-ul(style="list-style-type: square").lista-ul.lista-ul--color
                  li 
                    i.fas.fa-angle-right 
                    p Siempre ser necesario analizar el entorno de trabajo de los usuarios, clientes e interesados en el proyecto (<i>stakeholders</i>).
                  li 
                    i.fas.fa-angle-right 
                    p Esta tcnica es muy til al documentar la situacin actual del proceso de negocio.
                  li 
                    i.fas.fa-angle-right 
                    p Puede ser activa o pasiva.
                  li 
                    i.fas.fa-angle-right 
                    p En la observacin pasiva el observador no hace preguntas, solo se limita a tomar notas y no interfiere en el desempeo de las operaciones.
                  li 
                    i.fas.fa-angle-right 
                    p En la observacin activa, el observador habla con los usuarios.
              
              p.text-small(numero="3" titulo="Entrevistas").m-3
                ul.lista-ul(style="list-style-type: square").lista-ul.lista-ul--color
                  li 
                    i.fas.fa-angle-right 
                    p Se realiza con los directos interesados o usuarios clave.
                  li 
                    i.fas.fa-angle-right 
                    p Direccionan a los usuarios hacia aspectos especficos a tener en cuenta.
                  li 
                    i.fas.fa-angle-right 
                    p Son demasiado tiles para obtener y documentar datos o informacin sobre los requerimientos y sus niveles de importancia.
                  li 
                    i.fas.fa-angle-right 
                    p Deben ser entrevistas formales e informales para recolectar mucha ms informacin. 
                  li 
                    i.fas.fa-angle-right 
                    p Se deben enfocar los objetivos de la entrevista.
                  li 
                    i.fas.fa-angle-right 
                    p Las preguntas cerradas son tiles y ayudan a confirmar y validar informacin. 
                  li 
                    i.fas.fa-angle-right 
                    p Las preguntas abiertas son tiles y ayudan a identificar informacin faltante.
                  li 
                    i.fas.fa-angle-right 
                    p El xito de las entrevistas depende del grado de conocimiento del entrevistador y el entrevistado. El entrevistado debe tener muy buena disposicin, suministrar toda la informacin, de igual forma debe existir un muy buen registro de la documentacin de la discusin y una excelente relacin entre las partes involucradas.
              
              p.text-small(numero="4" titulo="Encuestas o cuestionarios").m-3
                ul.lista-ul(style="list-style-type: square").lista-ul.lista-ul--color
                  li 
                    i.fas.fa-angle-right 
                    p Tcnica til para recolectar requerimientos eficientes y necesarios de muchas personas.
                  li 
                    i.fas.fa-angle-right 
                    p Deben tener identificado el propsito y la audiencia, definir fechas precisas, preguntas claras y precisas.
                  li 
                    i.fas.fa-angle-right 
                    p Enfocarse en los objetivos de negocio necesarios a identificar.
                  li 
                    i.fas.fa-angle-right 
                    p Apoyarse en entrevistas de seguimiento e individualizar usuarios.
                  li 
                    i.fas.fa-angle-right 
                    p En su contenido debe haber preguntas abiertas y cerradas.

              p.text-small(numero="5" titulo="Mesas de trabajo <i>(workshops)</i>").m-3
                ul.lista-ul(style="list-style-type: square").lista-ul.lista-ul--color
                  li 
                    i.fas.fa-angle-right 
                    p Tcnica efectiva para analizar informacin rpida de varias personas.
                  li 
                    i.fas.fa-angle-right 
                    p Es necesario agendarse en tareas predefinidas y preseleccionar participantes, ajustando buenas prcticas y mejoras continuas de los procesos.
                  li 
                    i.fas.fa-angle-right 
                    p Es conveniente utilizar un facilitador y un transcriptor.
                  li 
                    i.fas.fa-angle-right 
                    p Se pueden analizar los resultados en diagramas o flujogramas, en reuniones.

              p.text-small(numero="6" titulo="Lluvia de ideas").m-3
                ul.lista-ul(style="list-style-type: square").lista-ul.lista-ul--color
                  li 
                    i.fas.fa-angle-right 
                    p Sesin de trabajo estructurada encaminada a obtener una cantidad de ideas posibles.
                  li 
                    i.fas.fa-angle-right 
                    p Es ideal limitarlas en tiempo y utilizar ayudas visuales con un facilitador.
                  li 
                    i.fas.fa-angle-right 
                    p Definir reglas de participacin, criterios para evaluar ideas, asignarles un puntaje, no permitir crticas y con lmite de tiempo de discusin.
                  li 
                    i.fas.fa-angle-right 
                    p Identificar la mayor cantidad de ideas y evaluarlas, considerar todas las ideas y respetar las de los dems.

              p.text-small(numero="7" titulo="Historias de usuario").m-3
                ul.lista-ul(style="list-style-type: square").lista-ul.lista-ul--color
                  li 
                    i.fas.fa-angle-right 
                    p Son simples conversaciones con los usuarios pero muy importantes para levantar requerimientos.
                  li 
                    i.fas.fa-angle-right 
                    p Es recomendable que sean escritas por los mismos usuarios o interesados, enfatizando en lo que el sistema debe realizar.
                  li 
                    i.fas.fa-angle-right 
                    p Al momento de redactar una historia deben tenerse en cuenta los roles o cargos de los usuarios, las funcionalidades y los resultados esperados de la aplicacin.
                  li 
                    i.fas.fa-angle-right 
                    p Las historias de usuario son una de las tcnicas ms importantes al momento de levantar requerimientos.
      .col-lg-5  
        img(src='@/assets/curso/imgtem04.jpg')(data-aos="fade-up")
  
    .row.mb-5
      .col-lg-10.caj
        .cajon.color-acento-botones.p-4.mb-4.mb-lg-0(style='background-color: #FEF4D6')
          h4 Los requerimientos:
          ul.lista-ul(style="list-style-type: square").lista-ul.lista-ul--color.lup
            li 
              i.fas.fa-angle-right 
              | Son las condiciones o capacidades que debe conservar un sistema, producto, servicio o componente para satisfacer las necesidades mediante un contrato, cumplir con estndares, especificaciones y dems documentos de manera formal.
            br
            li 
              i.fas.fa-angle-right 
              | Tambin se definen como las capacidades necesarias para que un cliente o usuario solucione un problema u objetivo ya preestablecido.
            br
            li 
              i.fas.fa-angle-right 
              | As mismo, se pueden relacionar los requisitos como las restricciones impuestas a un usuario por algunos interesados, definiendo objetivos precisos de un proyecto.
    
    .row.mb-2
      p(data-aos="zoom-in") Las restricciones siempre sern necesarias para lograr manipular o editar el manejo de la aplicacin de manera controlada, efectiva y funcional, para que se lleven a cabo los procesos de manera eficaz en la operacin de las tareas.

    .row.justify-content-start.align-items-center
      .col-lg-3.cajontitle2.mb-4(data-aos="zoom-in")      
        h3.text.mb-0 Restricciones
    
    .row.mb-2
      p(data-aos="zoom-in") Hacen nfasis, por ejemplo, en los permisos de accesos a usuarios en el sistema.
      br
      p(data-aos="zoom-in") Todos los usuarios del sistema lo podrn operar, pero no todos tienen los mismos derechos en la aplicacin.
      br
      p(data-aos="zoom-in") As como existen operadores de la aplicacin, habr usuarios con permisos de administradores que podrn manipular ms a fondo la aplicacin. 
    Separador

    .titulo-segundo.color-acento-contenido(data-aos="zoom-in")
      #t_1_1.h2 1.1 Caractersticas de requerimientos
    .row.mb-4
      p(data-aos="zoom-in") Los requerimientos deben cumplir con cierto tipo de caractersticas y criterios:
    
    .row.mb-4
      .col-lg-7
        .row.mb-5(data-aos="zoom-in-right")
          .col-md-12.mb-5.mb-md-0
            LineaTiempoD.color-acento-contenido.text-bold
              p.text-small(numero="1" titulo="Verificable").m-3
                ul(style="list-style-type: square")
                  p El requisito debe tener una implementacin precisa y comprobada, la prueba de consulta debe ser correcto o incorrecto.
                  
              p.text-small(numero="2" titulo="nico").m-3
                ul(style="list-style-type: square")
                  p Debe ser interpretado de manera exclusiva, de una sola manera.

              p.text-small(numero="3" titulo="Claro").m-3
                ul(style="list-style-type: square")
                  p Los requerimientos deben ser precisos, sin ms terminologas, de forma clara y simple.

              p.text-small(numero="4" titulo="Viable").m-3
                ul(style="list-style-type: square")
                  p El objetivo de los requerimientos es que sea realizable segn restricciones de tiempo, dinero, recursos y dems elementos a tener en cuenta.

              p.text-small(numero="5" titulo="Necesario").m-3
                ul(style="list-style-type: square")
                  p Si al omitirlo no se tiene ningn efecto, entonces no ser necesario.

              p.text-small(numero="6" titulo="Independiente").m-3
                ul(style="list-style-type: square")
                  p Para comprender los requerimientos no deben ser necesarios el conocimiento de otros.

              p.text-small(numero="7" titulo="Consistente").m-3
                ul(style="list-style-type: square")
                  p No deben existir conflicto entre ellos. 

              p.text-small(numero="8" titulo="Redundancia").m-3
                ul(style="list-style-type: square")
                  p Deben ser formulados una sola vez, y no sobreponerse o relacionarse de la misma forma con otro requerimiento.

              p.text-small(numero="9" titulo="Completo").m-3
                ul(style="list-style-type: square")
                  p Tambin debe ser especfico, se deben tener en cuenta todas las condiciones que se pueden llegar a presentar.
      .col-lg-5
        img.imgpos3(src='@/assets/curso/imgtem05.jpg')(data-aos="zoom-in-left")

    .row.justify-content-start.align-items-center
      .col-lg-4.cajontitle3.mb-4(data-aos="zoom-in")      
        h3.text.mb-0 Estructura de los requerimientos 
  
    p.mb-5(data-aos="zoom-in") Los requisitos se pueden dividir, segn su origen y caractersticas, se pueden representar mediante una grfica, situando las necesidades de los <i>stakeholders</i> como se muestra en la siguiente imagen:
    
    .row.mb-5
      img.col-10.imgpos4(src='@/assets/curso/requerimientos.svg')(data-aos="zoom-in")
    

    p(data-aos="zoom-in") Para estos tipos de necesidades depender mucho del analista de negocio decidir cul ser el nivel de detalle de cada nivel, aunque es adecuado, en algunos casos, detallar con ms precisin algunos requerimientos en el nivel de las necesidades.
    
    .row.justify-content-start.align-items-center
      .col-lg-3.cajontitle4.mb-4(data-aos="zoom-in")
        h3.text.mb-0 Qu es la trazabilidad?
    
    .row.mb-5
      .col-lg-7(data-aos="zoom-in-right")
        p Es la documentacin de la vida de cada uno de los requerimientos, permitiendo el historial de su formulacin original hasta el documento final
        p <b>Se debe documentar cada paso de manera estricta para conseguir la trazabilidad.</b>
        p Los requerimientos y necesidades surgen de diversas fuentes, como el cliente, el manager, los usuarios, etc., todos y cada uno comprenden de diferentes formas los requerimientos, y es en este momento, donde los requerimientos se deben precisar para poder planear y llevarlo a la ejecucin.<br><br>Utilizando la trazabilidad se sigue el historial de cada caracterstica implementada hasta las personas o grupos que solicitaron la generacin de los requerimientos, de esta manera, se permite un anlisis ms rpido en cada fase del proyecto.
      .col-lg-5
        img.imgpos5(src='@/assets/curso/imgtem07.jpg')(data-aos="zoom-in-left")
    
    .row.justify-content-start.align-items-center
      .col-lg-3.cajontitle4.mb-4(data-aos="zoom-in")
        h3.text.mb-0 Tipos de requerimientos
    
    .row.mb-4
      p(data-aos="zoom-in") Los requerimientos se clasifican en:
    
    .row.mb-3
      .titulo_principal(data-aos="zoom-in")
        h3 Requerimientos funcionales
    
    .row.align-items-center.mb-5
      .col-lg-8
        .cajon.color-primario.p-4.mb-4.mb-lg-0(style='background-color: #ECF7FF')
          p.mb-0 Son las declaraciones de los servicios que prestar el sistema, es la forma como reacciona a determinadas funciones. Del mismo modo, podran ser la manera de interactuar con otros sistemas, el tipo de respuestas automticas y los procesos predefinidos. En otros casos tambin los requisitos funcionales establecen de manera precisa lo que el sistema no debe hacer, es decir tambin puede ser una declaracin negativa.    
        p.m-2 De igual forma, los requerimientos funcionales tambin son las caractersticas que tendr el sistema o aplicacin, son las funciones que actan directamente con los usuarios, por ejemplo, las pantallas que permiten utilizar dicha aplicacin, lo que se puede visualizar, como Facebook, Instagram, etc., sus mens, diseos, botones, colores y dems funcionalidades que hacen muy amigable la interaccin con el usuario.<br><br/>Por otra parte, los mayores problemas se encuentran cuando existen especificaciones de requisitos inexactas o incoherentes. Si un analista de los requerimientos del sistema <b>toma suposiciones como conocimientos universales, podra conllevar un riesgo para el sistema</b>, es mejor siempre ser preciso y definirlo. Estos problemas se pueden encontrar en las funciones comunes relacionadas con las experiencias de usuario.
      .col-lg-4
        .imagen        
          img(src='@/assets/curso/imgtem08.jpg', style="width: 400px")     
    
    .row.cajon.p-4.mb-5(data-aos="zoom-in")(style='background-color: #FEF4D6')
        .col-2
          img(src='@/assets/curso/imgtem09.png' style="width: 90px")
        .col-10
          p.m-1 El analista es el responsable de la documentacin, por lo tanto, el mismo deber tratar de asegurarse que no hay lagunas de comprensin, por eso es importante tener en cuenta las historias de usuario, asegurndose de que todo el equipo est sincronizado respecto a los requisitos, objetivos e implementacin.
    
    .row.mb-5
      p(data-aos="zoom-in") Algunos ejemplos de requerimientos funcionales con caractersticas que permiten utilizar la aplicacin de manera cmoda y visual, ya sea en el diseo, men, botones, entre otros, son:
    
    .row.mb-5
      SlyderE(indicadores="derecha")(data-aos="zoom-in")
        .row
          .col-md-4.mb-2.mb-md-0
            h3 Interfaz de captura
            br
            p Incluye:
            br
            ul.lista-ul(style="list-style-type: square").lista-ul.lista-ul--color
                li 
                  i.fas.fa-angle-right 
                  p Las funciones que actan directamente con el usuario.
                li 
                  i.fas.fa-angle-right 
                  p Las pantallas y opciones que permiten utilizar la aplicacin.        
          .col-md-8
            figure
              img(src='@/assets/curso/imgtem10.jpg')
            
        .row
          .col-md-4.mb-2.mb-md-0
            h3 Funcionalidad especfica
            br
            p Por ejemplo, pedir un taxi:
            br
            ul.lista-ul(style="list-style-type: square").lista-ul.lista-ul--color.lup2
              li 
                i.fas.fa-angle-right 
                p Seleccionar el punto de partida y punto de llegada.
              li 
                i.fas.fa-angle-right 
                p Seleccionar el tipo de vehculo, forma de pago.
              li 
                i.fas.fa-angle-right 
                p Confirmar el nmero de telfono.
              li 
                i.fas.fa-angle-right 
                p Completar el viaje.

            p En este tipo de aplicaciones, se puede tambin calificar a los conductores y compartir los viajes, cada uno de estos sera un requerimiento funcional.

          .col-md-8
            figure
              img(src='@/assets/curso/imgtem11.jpg')
    
    .row.mb-4
      .titulo_principal(data-aos="zoom-in")
        h3 Requerimientos no funcionales
  
    .row.align-items-center.mb-4
      .col-lg-8    
        p.m-2 Son los requerimientos que no interfieren con las funciones especficas que entrega el sistema, asegurando la fiabilidad, tiempos de respuesta y capacidad de almacenamiento. En otras palabras, no habla de lo que hace el sistema, sino de cmo lo hace, define restricciones de entrada y salida, representando datos utilizados en las interfaces del sistema.<br><br>Puede ser con lenguaje de programacin Java, con alta velocidad de procesamiento de datos, para un ptimo funcionamiento de la aplicacin, disponibilidad, entre otros. Son los requisitos que debe tener la aplicacin para que funcione adecuadamente, se encargan de que la aplicacin cumpla con lo que tiene que hacer.
        .cajon.color-acento-botones.p-4.mb-5.mb-lg-0
            p.mb-0 <b>Los requerimientos no funcionales</b> son los requisitos que debe tener la aplicacin para que funcione adecuadamente; estos se encargan de que la aplicacin cumpla con lo que se quiere hacer, por otro lado, los requerimientos funcionales son todo lo que el usuario ve y manipula.    
      .col-lg-4
        .imagen        
          img(src='@/assets/curso/imgtem12.jpg', style="width: 450px")    

    p.mb-4(data-aos="zoom-in-right") A continuacin, se muestran ejemplos de requisitos no funcionales dentro de una aplicacin para pedir un taxi:
    
    .row.mb-4
      .col-lg-12
        .row.justify-content-center
          .col-lg-4.p-0.m-3.tarjeta-apilado.azul1
            img(src='@/assets/curso/tar1.jpg', alt='', class='img-fluid')
            h3.text-center.m-4 Operacin 7x24
            p.m-5.y-4 La aplicacin debe operar las 24 horas del da durante todo el ao.<br><br/>Hace referencia a la disponibilidad, nunca debe fallar y estar siempre disponible.
          
          .col-lg-4.p-0.m-3.tarjeta-apilado.azul1
            img(src='@/assets/curso/tar2.jpg', alt='', class='img-fluid')
            h3.text-center.m-4 Registro de usuario
            p.m-5.y-4 Para su uso se requieren datos de registro.<br><br/>Esta opcin de seguridad es un requisito que no se puede ver, pero es necesario para que la aplicacin funcione; es un requisito no funcional. 
    
    .row.mb-4
      .cajon-b.colorcajon7(data-aos="zoom-in")
        p 
          span.text-bold Los requerimientos no funcionales no se pueden ver ni tocar
          span , porque estn detrs de la aplicacin, para que funcione de manera rpida, segura y en cualquier momento. 
    separador

    .row.mb-5
      .titulo-segundo.color-acento-contenido(data-aos="zoom-in")
        #t_1_2.h2 1.2 Documentacin de requerimientos
    
    .row.mb-4(data-aos="zoom-in-right")
      .col-lg-8
